<template>
  <div class="warp">
    <div class="login">
      <img src="./imgs/二维码.jpg" width="160" height="160"
        style="position: absolute ; right: 20px; top: 20px; border-radius: 10px;" alt="">
      <div class="content-box">
        <div class="content">
          <img src="./imgs/米话.jpg" width="148" height="148" style="position: absolute; left:calc(50% - 74px);top:-74px;"
            alt="">
          <div class="content-left">
            <div class="content-left-pic"></div>
          </div>
          <div class="content-right">
            <img src="./imgs/呼叫中心.jpg" width="320" height="48"
              style="font-size: 26px; margin-top: 92px; margin-bottom: 39px;" alt="">
            <!-- //表单 -->
            <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="login-form" v-if="showLoginForm">
              <el-form-item label="用户名" prop="name">
                <el-input v-model="form.name" auto-complete="off" prefix-icon="el-icon-user"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="pwd">
                <el-input type="password" v-model="form.pwd" auto-complete="off" show-password
                  prefix-icon="el-icon-lock"></el-input>
              </el-form-item>
              <el-form-item label="验证码" prop="vercode" style="position: relative;">
                <el-input v-model="form.vercode" auto-complete="off" prefix-icon="el-icon-chat-dot-round"></el-input>
                <div class="form-div" style="position: absolute; top: 0px;right:-60px">
                  <span class="captcha-span" style="display: inline-block; margin-right:30px">{{ currentCaptcha
                    }}</span>
                  <el-button @click="generateCaptcha" type="text">换一下</el-button>
                </div>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" style="width:100%;" @click="handleSubmit">登 录</el-button>
              </el-form-item>
              <el-form-item>
                <p v-if="showLoginForm && !showRegisterForm">如果你没有注册的话请先<el-button type="text" style="margin-left:5px;"
                    @click="toggleForm('register')">注册</el-button>
                </p>
              </el-form-item>
            </el-form>
            <!-- //表单 -->
            <el-form :model="registerForm" :rules="registerRules" ref="registerForm" label-width="80px"
              class="register-form" v-if="showRegisterForm">
              <el-form-item label="用户名" prop="name">
                <el-input v-model="registerForm.name" auto-complete="off" prefix-icon="el-icon-user"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="pwd">
                <el-input type="password" v-model="registerForm.pwd" auto-complete="off"
                  prefix-icon="el-icon-lock"></el-input>
              </el-form-item>
              <el-form-item label="邮箱" prop="emails">
                <el-input v-model="registerForm.emails" auto-complete="off" prefix-icon="el-icon-message"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" style="width:100%;" @click="handleRegister">注 册</el-button>
              </el-form-item>
              <el-button type="text" @click="toggleForm('login')">返回登录</el-button>
            </el-form>

            <!-- 注册成功后返回登录的提示及按钮 -->
            <p v-if="registrationSuccess">注册成功！<el-button type="text" @click="toggleForm('login')">返回登录</el-button></p>
            <!-- 注册表单 -->
          </div>
        </div>
        <div class="footer">
          <p style="text-align: center;">
            Copyright © 2018-2024 EMI. ALL rights reserved
            南京易米云通网络科技有限公司
            苏ICP备14035390号-4
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { GetCode, PostRegister, PostLogin } from "../api/https";
export default {
  data() {
    return {
      showLoginForm: true,
      showRegisterForm: false,
      registrationSuccess: false,
      // 注册表单的数据
      registerForm: {
        name: '',
        pwd: '',
        emails: ''
      },
      registerRules: {
        name: [{ required: true, message: '请注册用户名', trigger: 'blur' }],
        pwd: [{ required: true, message: '请注册密码', trigger: 'blur' }],
        emails: [{ required: true, message: '请注册邮箱地址', trigger: 'blur' },
        { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }]
      },

      form: {
        name: '',
        pwd: '',
        vercode: ''
      },
      currentCaptcha: '', // 当前显示的验证码
      // 验证码相关规则可以加在这里
      rules: {
        name: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        pwd: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        vercode: [
          { validator: this.validateCaptcha, trigger: 'blur' }
        ]
      }
    };
  },
  created() {
    this.generateCaptcha(); // 页面创建时生成一次验证码
  },
  methods: {
    //注册
    toggleForm(formType) {
      this.showLoginForm = formType === 'login';
      this.showRegisterForm = formType === 'register';
      this.registrationSuccess = false; // 如果是从注册返回登录，清除注册成功的提示
    },
    handleRegister() {
      // 这里添加注册逻辑，验证表单，发送请求等
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          // 发送表单数据到后端
          console.log(this.registerForm);
          PostRegister(this.registerForm).then(res => {
            console.log(res);
            if (res.msg == "用户名重复") {
              this.$alert('<strong>用户名重复！</strong>', '米话呼叫中心', {
                  dangerouslyUseHTMLString: true
              });
            } else {
              this.registerForm = "",
                //注册成功弹窗
                this.$alert('<strong>已为您注册账号！</strong>', '米话呼叫中心', {
                  dangerouslyUseHTMLString: true
                });
              this.toggleForm('login')
            }
          })
        } else {
          console.log('注册失败');
        }
      });
    },
    ///登录
    handleSubmit() {
      this.$refs.form.validate(valid => {
        if (valid) {
          // 发送表单数据到后端
          PostLogin(this.form).then(res => {
            if (res.code == 1) {
              this.$alert('<strong>账号或密码错误！</strong>', '米话通话中心', {
                dangerouslyUseHTMLString: true
              });
              console.log(res.msg);
            } else {
               console.log(res);
            this.$alert('<strong>欢迎您！</strong>', '米话通话中心', {
              dangerouslyUseHTMLString: true
            });
            this.$router.push('/home');
            }
          })
        } else {
          console.log('请完善登录信息后重试');
        }
      });
    },
    generateCaptcha() {
      GetCode().then(res => {
        this.currentCaptcha = res
      })
    },
    validateCaptcha(rule, value, callback) {
      if (value === "") {
        callback(new Error('验证码为空'));
        return
      }
      if (value !== this.currentCaptcha) {
        callback(new Error('验证码输入错误'));
        return
      }
      else {
        callback();
      }
    },
  }
}
</script>
